._checks_wrap {
  margin-bottom: 15px;
  padding: 15px 20px;
  border-top: 2px solid #333333;
  border-bottom: 1px solid #d0d0d0; }

._checks {
  display: inline-block;
  position: relative;
  width: 24%; }

._checks input[type="radio"] {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

._checks input[type="radio"] + label {
  display: inline-block;
  position: relative;
  line-height: 30px;
  padding-left: 30px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none; }

._checks input[type="radio"] + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 3px;
  width: 20px;
  height: 20px;
  text-align: center;
  background: #fff;
  border: 1px solid #d0d0d0;
  border-radius: 100%;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  -ms-border-radius: 100%;
  -o-border-radius: 100%; }

._checks input[type="radio"]:checked + label:before {
  background: #fff;
  border-color: #adb8c0; }

._checks input[type="radio"]:checked + label:after {
  content: '';
  position: absolute;
  top: 9px;
  left: 6px;
  width: 10px;
  height: 10px;
  background: #333;
  border-radius: 100%; }

._checks input[type="radio"] + label span {
  display: inline-block;
  line-height: 30px;
  font-size: 16px; }

._check_payment {
  margin-bottom: 40px; }

